.home-page-wrapper {
  width: 60%;
  margin: 0 auto; }
  .home-page-wrapper .user-repo-form .title {
    text-align: center; }
  .home-page-wrapper .user-repo-form .githubform input[type="text"] {
    min-height: 60px;
    padding: 6px 8px;
    font-size: 25px;
    line-height: 20px;
    border: 1px solid #d1d5da;
    border-radius: 20px;
    outline: none;
    -webkit-box-shadow: inset 0 1px 2px rgba(27, 31, 35, 0.075);
            box-shadow: inset 0 1px 2px rgba(27, 31, 35, 0.075);
    float: left;
    width: 80%;
    background: #FFF; }
  .home-page-wrapper .user-repo-form .githubform button {
    float: left;
    width: 15%;
    cursor: pointer;
    margin-left: 20px;
    min-height: 58px;
    border-radius: 20px;
    font-size: 25px; }
  .home-page-wrapper .user-repo-form .githubform::after {
    content: "";
