    display: table; }
  .home-page-wrapper .user-repo-form input[type="text"]:focus {
    width: 80%; }
  .home-page-wrapper .user-repo-form input[type="text"]:focus ~ button {
    background: #006699; }
  .home-page-wrapper .user-repositories-list {
    padding: 20px 0; }
    .home-page-wrapper .user-repositories-list ul {
      list-style: none;
      padding: 0;
      margin: 0; }
    .home-page-wrapper .user-repositories-list .border-bottom {
      border-bottom: 1px #e1e4e8 solid; }
      .home-page-wrapper .user-repositories-list .border-bottom .title {
        display: inline-block; }



