.list-page-wrapper {
  width: 80%;
  margin: 40px auto; }
  .list-page-wrapper .user-issues-list {
    margin: 20px 0;
    border: 1px solid #e1e4e8;
    border-radius: 10px; }
    .list-page-wrapper .user-issues-list ul {
      list-style: none;
      padding: 0;
      margin: 0; }
      .list-page-wrapper .user-issues-list ul .border-bottom:last-child {
        border-bottom: 0; }
    .list-page-wrapper .user-issues-list .border-bottom {
      border-bottom: 1px solid #e1e4e8; }
      .list-page-wrapper .user-issues-list .border-bottom .title {
